import { CardapioItemDTO } from './../../../../models/domain/cardapio-item.dto';
import { Injectable } from '@angular/core';
import { HttpHeaders, HttpClient, HttpResponse } from '@angular/common/http';
import { Observable } from 'rxjs/Rx';

import { API_CONFIG } from './../../../../config/core/api.config';
import { AbstractService } from 'config/core/abstract-service';

import { CardapioDTO } from './../../../../models/domain/cardapio.dto';


@Injectable()
export class CardapioService extends AbstractService {

  url: string = `${API_CONFIG.baseUrl}/cardapio`;

  constructor(public http: HttpClient) {
    super();
  }

  findById(id: string): Observable<{}> {
    return this.http.get(`${this.url}/${id}`, this.getHearderToken());
  }

  findAll(): Observable<CardapioDTO[]> {
    return this.http.get<CardapioDTO[]>(`${this.url}/all`, this.getHearderToken());
  }
  
  salvar(cardapio: CardapioDTO) {
    return this.http.post(`${this.url}`, JSON.stringify(cardapio), this.getHearderTokenNoResponse());
  }

  excluir(cardapio: CardapioDTO): Observable<{}> {
    return this.http.delete(`${this.url}/${cardapio.id}`, this.getHearderToken());
  }

  changeStatus(cardapio: CardapioDTO) {
    return this.http.put(`${this.url}/${cardapio.id}/changeStatus`, {}, this.getHearderTokenNoResponse());
  }

  atualizar(cardapio: CardapioDTO) {
    return this.http.put(`${this.url}/${cardapio.id}`, JSON.stringify(cardapio), this.getHearderTokenNoResponse());
  }


  salvarItem(cardapioItem: CardapioItemDTO) {
    return this.http.post(`${this.url}/item`, JSON.stringify(cardapioItem), this.getHearderTokenNoResponse());
  }

  excluirItem(cardapioItem: CardapioItemDTO): Observable<{}> {
    return this.http.delete(`${this.url}/item/${cardapioItem.id}`, this.getHearderToken());
  }
  
  findAllByCardapio(idCardapio: string): Observable<CardapioItemDTO[]> {
    return this.http.get<CardapioItemDTO[]>(`${this.url}/item/?idCardapio=${idCardapio}`);
  }
}



